.App { min-height: 100vh; display:flex; flex-direction:column; }

.main-content { flex:1; }

/* Header tweaks to match preview */
.header {
  position: sticky;
  top: 0;
  z-index: 60;
  background: #000; /* black nav */
  color: #fff;
  /* remove any translucent glass so hero sits flush and nav is solid black */
  -webkit-backdrop-filter: none;
  backdrop-filter: none;
}

.header .container { display:flex; justify-content:space-between; align-items:center; }

/* logo and basic nav link styles for good contrast */
.logo { color: #0a0a0a; text-decoration: none; font-weight: 700; }
  .logo { display:flex; align-items:center; gap:0; }
.logo-badge { background: transparent; border-radius: 9999px; display: inline-flex; align-items: center; justify-content: center; padding: 0; box-shadow: none; }
  .logo-badge { padding: 0; }
.logo-img { height: 32px; width: auto; display:block; }
.logo-text { color: inherit; font-weight: 700; font-size: 1rem; line-height: 1; white-space: nowrap; }

/* Slightly larger badge and logo on desktop */
@media (min-width: 993px) {
  .logo-badge { padding: 8px; }
  .logo-img { height: 36px; }
}

/* Smaller badge/logo on very small screens */
@media (max-width: 480px) {
  .logo-badge { padding: 4px; }
  .logo-img { height: 26px; }
  /* Keep the logo text visible on small screens but reduce size to fit */
  .logo-text { display: inline-block; font-size: 0.95rem; line-height: 1; color: var(--text) !important; }
}
.nav-link { color: #fff; text-decoration: none; padding: 8px 10px; }
.nav-link:hover, .nav-link.active { color: #f1f1f1; }

/* make the CTA (inverted) pop on a black nav */
.nav-link.btn.inverted { background: #fff; color: #000; border-radius: 4px; }

/* Hamburger button (desktop-hidden normally) */
.hamburger { background: transparent; border: none; display: inline-flex; gap: 6px; align-items: center; padding: 8px; color: #fff; cursor: pointer; }
.hamburger:focus { outline: 3px solid rgba(255,255,255,0.16); outline-offset: 3px; border-radius: 6px; }
.hamburger:focus-visible { outline: 3px solid rgba(255,255,255,0.22); }
.hamburger .bar { display: block; width: 18px; height: 2px; background: #fff; transition: transform .18s ease, opacity .18s ease; }

/* Mobile nav overlay */
.mobile-nav { position: fixed; left: 0; right: 0; top: var(--header-height, 64px); background: #000; color: #fff; padding: 1rem; transform: translateY(-6px); opacity: 0; visibility: hidden; transition: opacity .18s ease, transform .18s ease, visibility .18s ease; z-index: 70; }
.mobile-nav.open { opacity: 1; visibility: visible; transform: translateY(0); }
.nav.mobile .nav-link { display: block; padding: 12px 0; color: #fff; }

/* Ensure mobile nav link colors override other rules and keep CTA readable */
#mobile-nav .nav-link { color: #fff; }
#mobile-nav .nav-link.btn.inverted { background: #fff; color: #000; padding: 10px 12px; border-radius: 4px; display: inline-block; }

/* Subtle divider and spacing tweaks */
.header { border-bottom: 1px solid rgba(255,255,255,0.06); box-shadow: 0 1px 0 rgba(255,255,255,0.02); }
.header .container { padding: 6px 12px; }

/* Mobile-specific adjustments: keep header black, hide desktop nav and ensure hamburger accessible */
@media (max-width: 992px) {
  .header { background: #000; }
  .desktop { display: none !important; }
  /* larger, easier-to-tap hamburger on mobile */
  .hamburger { display: inline-flex; padding: 12px; border-radius: 8px; }
  .hamburger .bar { width: 22px; height: 3px; }

  /* mobile nav should fill remaining viewport and be scrollable for long menus */
  .mobile-nav {
    top: var(--header-height, 56px);
    bottom: 0;
    height: calc(100vh - var(--header-height, 56px));
    padding: 1.25rem 1.25rem 1.5rem;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
  }

  /* bigger touch targets and clearer typography */
  .nav.mobile { display: flex; flex-direction: column; gap: 6px; }
  .nav.mobile .nav-link { padding: 16px 0; font-size: 1.125rem; line-height: 1.4; }

  /* make CTA clearly tappable and full-width on mobile */
  #mobile-nav .nav-link.btn.inverted { display: block; width: 100%; text-align: center; padding: 12px 14px; }

  /* reduce motion for users on mobile and keep open/close snappy */
  .mobile-nav { transition: opacity .12s ease, transform .12s ease; }
}
/* Hide hamburger on desktop viewports >= 993px */
@media (min-width: 993px) {
  .hamburger { display: none !important; }
}

/* Strong override to ensure mobile menu contrast (force white text on black background) */
#mobile-nav, .mobile-nav { background: #000 !important; color: #fff !important; }
#mobile-nav .nav.mobile a, #mobile-nav .nav-link { color: #fff !important; background: transparent !important; }
#mobile-nav .nav.mobile .btn.inverted, #mobile-nav .nav-link.btn.inverted { background: #fff !important; color: #000 !important; }

/* Keyboard focus for nav links */
.nav-link:focus-visible { outline: 3px solid rgba(255,255,255,0.12); outline-offset: 3px; border-radius: 4px; }

.nav { display:flex; gap:12px; list-style:none; align-items:center; }

/* Page sections */
.page { padding: 0 0 3.5rem; /* no top padding so hero can sit flush under sticky header */ }

/* add top spacing to the main section when present so pages without a hero keep vertical rhythm */
.page > .section { padding-top: 3.5rem; }

/* Hero card */
.hero { display:flex; align-items:center; justify-content:center; }
.hero-card { text-align:left; }

.section h2 { text-align:left; margin-bottom:1.25rem; color:var(--text); }

.features { display:grid; grid-template-columns: repeat(auto-fit, minmax(260px, 1fr)); gap:1rem; margin-top:1rem; }

.feature { text-align:left; }

.service-card { transition: transform 0.18s ease; }
.service-card:hover { transform: translateY(-6px); }

.form-group input, .form-group textarea { background: transparent; border: 1px solid rgba(0,0,0,0.08); color: var(--text); }

@media (max-width:768px) {
  .page { padding: 2rem 0; }
  .features { grid-template-columns: 1fr; }
}

/* Mobile UX niceties */
/* backdrop behind mobile nav */
.mobile-backdrop {
  position: fixed;
  left: 0;
  right: 0;
  top: var(--header-height, 56px);
  bottom: 0;
  background: rgba(0,0,0,0.36);
  z-index: 65;
}

/* ensure mobile nav respects safe area and allows comfortable bottom padding */
#mobile-nav, .mobile-nav {
  padding-bottom: calc(env(safe-area-inset-bottom, 0px) + 1.5rem);
}

/* touch and motion improvements for mobile */
button, .hamburger, .nav-link { touch-action: manipulation; }

@media (prefers-reduced-motion: reduce) {
  * { transition-duration: 0.01ms !important; animation-duration: 0.01ms !important; }
}

/* Slightly smaller header padding on very small devices to maximize viewport */
@media (max-width: 480px) {
  .header .container { padding: 6px 12px; }
  .logo { font-size: 1rem; }
  .logo-img { height: 28px; }
}
