@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap');


:root {
  --bg: #ffffff; /* white background */
  --header-height: 44px;
  --container-width: 100%; /* allow full viewport width (bleed) */
  --panel: rgba(255,255,255,0.7); /* translucent panel for glass look */
  --muted: rgba(0,0,0,0.6);
  --text: #0a0a0a; /* near-black text */
  --accent: #0a0a0a;
  --glass-border: rgba(0,0,0,0.06);
  --radius: 14px;
  --card-shadow: 0 8px 30px rgba(10,10,10,0.06);
}

html, body, #root { height: 100%; }

body {
  margin: 0; /* remove default browser margin to avoid gaps */
  background: #ffffff !important; /* ensure page background is white */
  color: var(--text);
  font-family: 'Roboto', system-ui, -apple-system, 'Segoe UI', 'Helvetica Neue', Arial;
  /* lock a base font-size to reduce cross-device scaling differences */
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container { width: 100%; margin: 0 auto; padding: 0 8px; box-sizing: border-box; }

/* Typography scale to better match PDF */
h1, .cover-title { font-family: 'Poppins', sans-serif; font-weight: 700; }
h2 { font-family: 'Poppins', sans-serif; font-weight: 600; }
h3 { font-family: 'Poppins', sans-serif; font-weight: 600; }
p, li, a { font-size: 16px; line-height: 1.6; }
/* Explicit typographic scale using Roboto */
h1, .cover-title { font-family: 'Roboto', sans-serif; font-weight: 700; font-size: 64px; line-height: 1.03; }
h2 { font-family: 'Roboto', sans-serif; font-weight: 700; font-size: 28px; line-height: 1.18; }
h3 { font-family: 'Roboto', sans-serif; font-weight: 500; font-size: 20px; line-height: 1.22; }
p, li, a { font-size: 16px; line-height: 1.6; }
p, li, a { font-size: 16px; line-height: 1.6; }

/* Utility: muted text tone */
.muted { color: rgba(10,10,10,0.6); }

/* Header: slim, translucent, elegant */
.header {
  background: rgba(255,255,255,0.92) !important;
  -webkit-backdrop-filter: blur(4px) saturate(110%);
  backdrop-filter: blur(4px) saturate(110%);
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 10000;
  padding: 4px 0; /* reduced vertical padding for a lower header */
  min-height: var(--header-height);
  box-shadow: 0 6px 18px rgba(10,10,10,0.04);
  border-bottom: 1px solid rgba(10,10,10,0.04);
  transition: background 180ms ease, padding 180ms ease, box-shadow 180ms ease;
}
.header .container { display:flex; align-items:center; justify-content:space-between; max-width: var(--container-width); margin: 0 auto; padding: 0 10px; box-sizing: border-box; }

/* Make logo flush to the left edge of the viewport and nav flush to the right */
.header .logo { position: static; z-index: 10001; margin-right: 0; top: auto; transform: none; }
.header .container .nav { position: static; right: auto; display: flex; gap: 12px; align-items: center; margin: 0; flex: none; justify-content: center; min-width: 0; transform: none; }

/* Desktop-only: show navigation inside a single black rounded rectangle with white text */
@media (min-width: 993px) {
  .desktop .nav {
    background: #000;
    color: #fff;
    /* make it pill-shaped with extra left/right padding and clip overflow */
    /* use a responsive padding so the pill grows with content but stays pleasant on very wide screens */
    padding: 6px clamp(18px, 2.5vw, 40px) !important;
    border-radius: 9999px !important;
    box-shadow: 0 8px 24px rgba(0,0,0,0.12);
    display: inline-flex;
    gap: 12px;
    align-items: center;
    justify-content: center;
    white-space: nowrap; /* keep items on a single line */
    overflow: visible; /* allow full nav content to be visible */
    flex-shrink: 0; /* don't let the pill shrink and let it expand to fit items */
  }
  .desktop .nav a, .desktop .nav .nav-link { color: #fff !important; }
  .desktop .nav a:hover, .desktop .nav .nav-link:hover { background: rgba(255,255,255,0.06); color: #fff !important; }

  /* make header transparent on desktop so the black nav rectangle is visible */
  .header { background: transparent !important; border-bottom: none !important; }
}

/* On smaller screens revert to normal flow so hamburger stacks with logo */
@media (max-width: 992px) {
  .header .logo { position: static; left: auto; }
  .header .container .nav { position: static; margin-left: auto; }
}

/* Enforce a fixed, compact header size on mobile so scrolling/up interactions
   can't leave the header visually larger. This forces height/min-height and
   removes extra vertical padding while keeping the inner container centered. */
@media (max-width: 992px) {
  .header {
    padding: 0 !important; /* avoid stacking paddings from other rules */
    height: var(--header-height) !important;
    min-height: var(--header-height) !important;
    display: block; /* ensure the header respects the explicit height */
    overflow: hidden; /* prevents children from expanding the header height */
    transition: background 160ms ease, height 120ms ease, padding 120ms ease;
  }
  .header .container {
    padding: 0 12px !important;
    height: 100%;
    display: flex;
    align-items: center;
    box-sizing: border-box;
  }
  /* Ensure mobile-nav positions relative to the fixed header height */
  .mobile-nav, .mobile-backdrop { top: calc(var(--header-height)); }
}
.logo { color: #0a0a0a; font-weight:700; font-size:15px; letter-spacing: -0.3px; display:flex; align-items:center; gap:6px; }
.nav a { color: var(--text) !important; font-weight:600; padding:6px 8px; border-radius:6px; text-decoration: none; transition: background 140ms ease, color 140ms ease, transform 140ms ease; font-size:14px; }
.nav a:hover { color: #000000; background: rgba(0,0,0,0.04); transform: translateY(-1px); }

/* nav-link base class used with NavLink */
.nav-link { text-decoration: none; color: rgba(10,10,10,0.8); display:inline-block; padding:6px 8px; border-radius:8px; transition: color 180ms ease, background 180ms ease; }
.nav-link:hover { background: rgba(0,0,0,0.04); color: #000; }

/* active page styling: white rounded background, black text */
.nav-link.active {
  background: rgba(0,0,0,0.04);
  color: #000000 !important;
  padding: 6px 12px;
  border-radius: 6px;
}

/* focus styles for keyboard users */
.nav-link:focus-visible {
  outline: 3px solid rgba(0,0,0,0.08);
  outline-offset: 3px;
}

/* Strong override: ensure desktop nav links are white inside the black nav rectangle.
   This is placed after the general `.nav a` rules to win even when those use !important. */
@media (min-width: 993px) {
  .desktop .nav a,
  .desktop .nav .nav-link,
  .header .container .nav a,
  .header .container .nav .nav-link {
    color: #ffffff !important;
    background: transparent !important;
  }
  /* ensure SVG icon colors inherit the text color */
  .desktop .nav svg { fill: currentColor !important; }
}

/* if CTA (Contact) is active, keep solid look but invert colors */
.nav-link.btn.active {
  background: #ffffff;
  color: #000000 !important;
}

/* Hero (glass card) */
.hero { display:flex; align-items:center; justify-content:center; padding:4.5rem 0 5.5rem; }
.hero-card {
  background: linear-gradient(180deg, rgba(255,255,255,0.98), rgba(255,255,255,0.96));
  border-radius: 14px;
  box-shadow: 0 20px 60px rgba(10,10,10,0.08);
  padding: 56px;
  width: 100%;
  max-width: 1100px;
  border: 1px solid rgba(0,0,0,0.04);
  backdrop-filter: none;
}
.hero-card h1 { margin:0 0 10px 0; font-size:36px; color:var(--text); }
.hero-card p { margin:0 0 18px 0; color:var(--muted); line-height:1.6; }

/* Buttons: minimalist outline / filled subtle */
.btn {
  background: #000000; /* primary button black */
  color: #ffffff; /* button text white */
  padding: 10px 16px;
  border-radius: 8px;
  font-weight: 700;
  border: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
}
.btn:hover { background: #111111; }
/* ensure links styled as buttons don't show underlines */
.btn, .btn.secondary, .btn.inverted { text-decoration: none !important; }

/* secondary/outline button variant (transparent) */
.btn.secondary {
  background: transparent;
  color: var(--text);
  border: 1px solid rgba(0,0,0,0.08);
}
.btn.secondary:hover { background: rgba(0,0,0,0.03); }

/* inverted variant for dark headers (white background, black text) */
.btn.inverted {
  background: #ffffff;
  color: #000000;
  border: 1px solid rgba(0,0,0,0.06);
}
.btn.inverted:hover { background: #f8f8f8; }

.feature, .service-card {
  /* unify card styling across the site to light glassmorphism */
  background: linear-gradient(180deg, rgba(255,255,255,0.92), rgba(255,255,255,0.88));
  border-radius: 12px;
  padding: 20px;
  border: 1px solid var(--glass-border);
  box-shadow: var(--card-shadow);
  color: var(--text);
}

/* make feature/service cards look more glassy and lift on hover */
.feature, .service-card, .glass-card {
  backdrop-filter: blur(6px) saturate(120%);
  transition: transform 220ms cubic-bezier(.2,.9,.2,1), box-shadow 220ms cubic-bezier(.2,.9,.2,1), border-color 220ms ease, background 220ms ease;
  will-change: transform, box-shadow;
}

.feature:hover, .service-card:hover, .glass-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 18px 40px rgba(10,10,10,0.08);
  border-color: rgba(0,0,0,0.09);
  cursor: pointer;
}

/* keyboard focus for accessibility */
.feature:focus-visible, .service-card:focus-visible, .glass-card:focus-visible {
  outline: 3px solid rgba(10,10,10,0.06);
  outline-offset: 4px;
}

table { color: var(--muted); }

/* Header: slim translucent bar (ensure nav text contrast) */
.header {
  background: rgba(255,255,255,0.6) !important;
  -webkit-backdrop-filter: blur(6px) saturate(120%);
  backdrop-filter: blur(6px) saturate(120%);
  position: sticky;
  top: 0;
  z-index: 10000;
  padding: 6px 0; /* reduced vertical padding for shorter nav */
  min-height: var(--header-height);
  box-shadow: 0 6px 18px rgba(10,10,10,0.045);
  border-bottom: 1px solid rgba(10,10,10,0.04);
  transition: background 220ms ease, padding 220ms ease, box-shadow 220ms ease;
}

/* Ensure nav link text is always visible on the header */
.header .nav a,
.header .nav-link,
.header .nav a:link,
.header .nav a:visited {
  color: #0a0a0a !important;
  opacity: 1 !important;
  text-shadow: none !important;
}

.header .nav-link.btn.inverted {
  background: rgba(255,255,255,0.9) !important;
  color: #0a0a0a !important;
  border: 1px solid rgba(10,10,10,0.06) !important;
}

/* Footer: dark background with white text */
.footer {
  background: #000000;
  color: #ffffff;
  padding: 3rem 0 3.5rem;
  border-top: 1px solid rgba(255,255,255,0.04);
}
.footer .container { display:flex; flex-direction:column; gap:6px; }
.footer p { margin: 0; color: #ffffff; }
.footer a { color: #ffffff; text-decoration: underline; }

/* Skip link: visible on keyboard focus only */
.skip-link {
  position: absolute;
  left: -9999px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
.skip-link:focus, .skip-link:active {
  left: 12px;
  top: 12px;
  width: auto;
  height: auto;
  padding: 8px 12px;
  background: #000;
  color: #fff;
  border-radius: 6px;
  z-index: 20000;
}

.glass-card h3 { margin: 0 0 8px 0; font-size: 18px; color: var(--text); }
.glass-card p { margin: 0; color: var(--muted); line-height: 1.5; }

@media (max-width:768px) { .hero { padding:3rem 0; } .hero-card { padding:22px; } .hero-card h1 { font-size:20px; } }

/* Cover hero styles for homepage */
.cover-hero {
  /* remove top padding so hero sits flush under the sticky header */
  min-height: 56vh; /* reduce hero height to better match PDF */
  display: flex;
  align-items: center;
  position: relative;
  padding: 0 0 3.5rem; /* flush to header but smaller bottom gap */
}

/* Ensure homepage cover hero has appropriate top spacing so header does not overlap content */
.page > .cover-hero { margin-top: 0; padding-top: calc(var(--header-height) + 12px); }

.cover-left {
  /* removed max-width to allow full-width bleed */
  padding-left: 6px;
  position: relative;
}

.cover-title {
  /* h1 already defines the main typographic size; add small margin tweak */
  margin-bottom: 14px;
}

.cover-sub {
  color: rgba(0,0,0,0.72); /* darker lead text for readability */
  font-size:18px;
  margin-bottom:24px;
  text-shadow: 0 1px 0 rgba(255,255,255,0.55), 0 6px 18px rgba(0,0,0,0.05);
}

.cover-cta { display:flex; gap:12px; align-items:center; }

@media (max-width:768px) {
  .cover-hero { padding:4rem 0; min-height: 56vh; }
    /* absolutely position the hamburger at the right edge and vertically center it */
    .hamburger {
      position: absolute !important;
      right: 12px !important;
      top: 50% !important;
      transform: translateY(-50%) !important;
      z-index: 10002 !important;
    }
    /* Hide the inline/desktop nav on mobile and rely on the hamburger + overlay menu */
    .header .container .nav,
    .desktop {
      display: none !important;
    }
  gap: 1.5rem;
  align-items: start;
}
.two-col .col h4 { margin: 0 0 8px 0; color: var(--text); }

.validated-list, .extensible-list {
  list-style: none;
  margin: 0;
  padding: 0;
  color: var(--muted);
}
.validated-list li {
  position: relative;
  padding-left: 28px;
  margin-bottom: 0.6rem;
}
.validated-list li::before {
  content: '✔';
  position: absolute;
  left: 0;
  top: 0;
  color: #000000; /* black checkmark */
  font-size: 14px;
  line-height: 1;
}

@media (max-width: 768px) {
  .two-col { grid-template-columns: 1fr; }
}

/* Technology page cards and headings */
.tech-card {
  /* stronger translucent glass panel */
  background: rgba(255,255,255,0.48);
  -webkit-backdrop-filter: blur(12px) saturate(130%);
  backdrop-filter: blur(12px) saturate(130%);
  border-radius: 12px;
  padding: 20px 24px;
  margin-bottom: 1.25rem;
  border: 1px solid rgba(255,255,255,0.18);
  box-shadow: 0 12px 40px rgba(10,10,10,0.06);
  color: var(--text);
}

.tech-card {
  transition: transform 220ms cubic-bezier(.2,.9,.2,1), box-shadow 220ms cubic-bezier(.2,.9,.2,1), border-color 220ms ease;
  will-change: transform, box-shadow;
}

.tech-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 18px 40px rgba(10,10,10,0.08);
  border-color: rgba(0,0,0,0.08);
}

.tech-card:focus-visible {
  outline: 3px solid rgba(10,10,10,0.06);
  outline-offset: 4px;
}

@media (max-width: 768px) {
  .tech-card:hover { transform: translateY(-4px); }
}

.section-title {
  font-size: 20px;
  font-weight: 700;
  color: var(--text);
  margin: 0 0 12px 0;
  padding-left: 8px;
  border-left: 4px solid rgba(0,0,0,0.06);
}

.tech-card p, .tech-card ul { color: var(--muted); }

.tech-card code {
  background: rgba(0,0,0,0.03);
  padding: 2px 6px;
  border-radius: 6px;
  font-size: 90%;
}

.tech-divider {
  height: 1px;
  background: rgba(0,0,0,0.04);
  margin: 14px 0;
}

.extensible-list li::before, .validated-list li::before, .tech-card ul li::before {
  /* ensure other lists have a subtle bullet if needed */
  content: '';
}

/* Section header with small icon */
.section-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 8px;
}
.section-icon svg {
  width: 20px;
  height: 20px;
  color: var(--text);
  flex: 0 0 20px;
}
.section-title {
  font-size: 18px;
  font-weight: 700;
  color: var(--text);
  margin: 0;
  padding: 0;
}

/* decorative background image behind hero content */
.cover-hero::before {
  /* disabled: using an inline <img> element for decorative artwork to avoid bundler URL resolution */
  display: none;
}

.cover-hero .container { position: relative; z-index: 5; }

/* Place the hero artwork behind the entire cover region (between header and
   the section boundary) so the image fills the full-bleed area and isn't
   constrained to the glass text card. Keep the translucent glass panel above
   the artwork. */
.cover-hero {
  /* interior pages use a simple white background with dark text */
  background: transparent;
  color: var(--text);
  position: relative;
  overflow: hidden;
}

/* binary overlay intentionally removed — hero will use only the background image + gradient */

/* Ensure the site's main container content remains above the digit overlay */
.cover-hero .container { position: relative; z-index: 5; }

/* Use dark hero text on a light background to match the PDF reference */
.cover-hero .cover-title {
  color: #0a0a0a;
  text-shadow: none;
}
.cover-hero .cover-sub {
  color: rgba(0,0,0,0.72);
  text-shadow: none;
}

/* decorative image placed in DOM via <img class="cover-circuit"> for better control and to avoid CSS url() processing */
.cover-circuit {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: auto;
  height: 100%;
  object-fit: cover;
  opacity: 0.06;
  pointer-events: none;
  z-index: 0;
}

/* ensure hero-card (translucent white) sits above decorative image */
.cover-left .hero-card {
  /* keep the translucent white glass card for hero text */
  background: linear-gradient(180deg, rgba(255,255,255,0.98), rgba(255,255,255,0.96));
  border-radius: 14px;
  box-shadow: 0 20px 60px rgba(10,10,10,0.08);
  padding: 40px;
  border: 1px solid rgba(0,0,0,0.04);
  color: var(--text);
}

/* Ensure text inside the translucent card uses dark colors to contrast */
.cover-left .hero-card h1,
.cover-left .hero-card .cover-title {
  color: #ffffff !important;
  text-shadow: none; /* no glow — pure white text on black background */
}
.cover-left .hero-card p,
.cover-left .hero-card .cover-sub {
  color: rgba(255,255,255,0.92) !important;
  text-shadow: none;
}

/* hero-panel uses the same translucent glass tokens but remove extra margins */
.hero-panel { padding: 32px; margin: 0; }

/* full-bleed hero panel that spans viewport width and feels integrated */
.hero-panel.full-bleed {
  /* absolutely cover the parent .cover-hero so the glass panel matches the
     full-bleed cover area exactly (full width and exact cover height). The
     parent `.cover-hero` must remain `position: relative` for this to work. */
  position: absolute;
  inset: 0; /* top:0; right:0; bottom:0; left:0; */
  width: 100%;
  border: none; /* remove border per request */
  border-radius: 0; /* full-bleed aligns to edges */
  box-shadow: 0 14px 50px rgba(10,10,10,0.06);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0; /* container will provide internal spacing */
  z-index: 6; /* ensure panel sits above cover background */
  overflow: hidden;
}

/* Ensure headings and lead text inside the cover don't introduce extra top margins that
   could cause a visible seam beneath the header */
.cover-left h1, .cover-title { margin-top: 0; }
.cover-left p, .cover-sub { margin-top: 0; }

/* Internal pages: make `.center-hero` titles/subtitles match the homepage
   scale and alignment while keeping homepage rules unchanged. This narrows
   the content column, left-aligns text, and adjusts font-sizes responsively. */
.center-hero .container {
  /* removed max-width constraint to allow full-width bleed */
  text-align: left; /* ensure title/sub are left aligned like homepage */
  padding-left: 0; /* container padding is handled by outer layout */
}
.center-hero .cover-title {
  font-size: 48px;
  line-height: 1.04;
  margin-bottom: 14px;
  color: #0a0a0a;
  font-weight: 700;
}
.center-hero .cover-sub {
  color: rgba(0,0,0,0.72);
  font-size: 18px;
  margin-bottom: 24px;
  text-shadow: none;
  /* removed max-width constraint to allow full-width bleed */
}

@media (max-width: 768px) {
  .center-hero .container { max-width: 100%; padding-left: 12px; padding-right: 12px; }
  .center-hero .cover-title { font-size: 22px; }
  .center-hero .cover-sub { font-size: 15px; }
}

/* ensure hero content keeps left padding on small screens */
.hero-panel.full-bleed .container { padding-left: 20px; padding-right: 20px; }

/* make hero more translucent and cloudy */
.hero-panel.full-bleed {
  /* remove glass effect for full-bleed hero panel — keep transparent so the
     underlying black hero shows through and text remains white */
  background: transparent;
  -webkit-backdrop-filter: none;
  backdrop-filter: none;
  border: none;
  box-shadow: none;
}

.hero-panel.full-bleed::before {
  content: '';
  position: absolute;
  inset: 0;
  /* stronger layered radial gradients to create a cloudy texture */
  background-image:
    radial-gradient(circle at 25% 30%, rgba(255,255,255,0.06), transparent 22%),
    radial-gradient(circle at 75% 70%, rgba(255,255,255,0.05), transparent 20%),
    url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='200' height='200' preserveAspectRatio='none'><filter id='n'><feTurbulence baseFrequency='0.9' numOctaves='2' stitchTiles='stitch'/><feColorMatrix type='saturate' values='0'/></filter><rect width='100%25' height='100%25' filter='url(%23n)' opacity='0.03' /></svg>");
  background-repeat: no-repeat, no-repeat, repeat;
  background-size: cover, cover, 200px 200px;
  pointer-events: none;
  z-index: 4;
  /* remove decorative cloudy overlay on hero when glass is disabled */
  display: none;
}

/* subtle vignette to darken edges and draw focus to center; sits above
   the cloudy overlay (::before) but below the content container */
.hero-panel.full-bleed::after {
  content: '';
  position: absolute;
  inset: 0;
  pointer-events: none;
  z-index: 5;
  /* transparent center with darkened edges; low opacity for subtlety */
  background-image: radial-gradient(ellipse at center, rgba(0,0,0,0) 48%, rgba(0,0,0,0.42) 100%);
  mix-blend-mode: multiply;
  opacity: 0; /* disable vignette so hero stays purely black */
}

.hero-panel.full-bleed .container { position: relative; z-index: 6; display:flex; align-items:center; width:100%; padding-left: 20px; padding-right: 20px; }

/* Centered minimal hero (typographic) for interior pages */
.center-hero {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  /* match the licensing page spacing so internal pages share the same
     visual rhythm: keep a header-sized top offset and a compact bottom gap */
  padding: calc(var(--header-height)) 0 1.5rem;
  min-height: 30vh;
  background: transparent; /* keep minimalist */
  position: relative; /* for decorative pseudo-element */
}
.center-hero .container { 
  position: relative; 
  z-index: 2; 
  /* removed max-width constraint to allow full-width bleed */
  margin: 0 auto;
  padding-left: 0;
  padding-right: 0;
}
